import axios from "axios";
import Cookies from "js-cookie";
import { getToken } from "@/utils/request/auth";
import router from "@/router";
import errorCode from "@/utils/request/errorCode";
import { ElNotification, ElMessageBox, ElMessage, Loading } from "element-plus";

export let isRelogin = { show: false };

export function tansParams(params) {
  let result = "";
  for (const propName of Object.keys(params)) {
    const value = params[propName];
    var part = encodeURIComponent(propName) + "=";
    if (value !== null && value !== "" && typeof value !== "undefined") {
      if (typeof value === "object") {
        for (const key of Object.keys(value)) {
          if (
            value[key] !== null &&
            value[key] !== "" &&
            typeof value[key] !== "undefined"
          ) {
            let params = propName + "[" + key + "]";
            var subPart = encodeURIComponent(params) + "=";
            result += subPart + encodeURIComponent(value[key]) + "&";
          }
        }
      } else {
        result += part + encodeURIComponent(value) + "&";
      }
    }
  }
  return result;
}

axios.defaults.headers["Content-Type"] = "application/json;charset=utf-8";
// axios
const service = axios.create({
  // baseURL: process.env.VUE_APP_BASE_API,
  baseURL: "http://81.68.103.96:8080/",

  timeout: 20 * 1000, //20s
});

// request
service.interceptors.request.use(
  (config) => {
    //  token
    const isToken = (config.headers || {}).isToken === false;
    // 
    const isRepeatSubmit = (config.headers || {}).repeatSubmit === false;
    if (getToken() && !isToken) {
      config.headers["Authorization"] = "Bearer " + getToken(); // token
    }
    // getparams
    if (config.method === "get" && config.params) {
      let url = config.url + "?" + tansParams(config.params);
      url = url.slice(0, -1);
      config.params = {};
      config.url = url;
    }
    if (
      !isRepeatSubmit &&
      (config.method === "post" || config.method === "put")
    ) {
      const requestObj = {
        url: config.url,
        data:
          typeof config.data === "object"
            ? JSON.stringify(config.data)
            : config.data,
        time: new Date().getTime(),
      };
      // const sessionObj = cache.session.getJSON('sessionObj')
      // if (sessionObj === undefined || sessionObj === null || sessionObj === '') {
      //   cache.session.setJSON('sessionObj', requestObj)
      // } else {
      //   const s_url = sessionObj.url;                  // 
      //   const s_data = sessionObj.data;                // 
      //   const s_time = sessionObj.time;                // 
      //   const interval = 1000;                         // (ms)
      //   if (s_data === requestObj.data && requestObj.time - s_time < interval && s_url === requestObj.url) {
      //     const message = '';
      //     console.warn(`[${s_url}]: ` + message)
      //     return Promise.reject(new Error(message))
      //   } else {
      //     cache.session.setJSON('sessionObj', requestObj)
      //   }
      // }
    }
    return config;
  },
  (error) => {
    console.log(error);
    Promise.reject(error);
  }
);

// 
service.interceptors.response.use(
  (res) => {
    // console.log("http res",res);
    // 
    // const code = res.data.code || 200;
    const code = res.status || 200;
    // console.log("code",code);
    // 
    const msg = errorCode[code] || res.data.msg || errorCode["default"];
    // 
    if (
      res.request.responseType === "blob" ||
      res.request.responseType === "arraybuffer"
    ) {
      return res.data;
    }
    if (code === 401) {
      if (!isRelogin.show) {
        isRelogin.show = true;
        ElMessageBox.confirm(
          "",
          "",
          {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning",
          }
        )
          .then(() => {
            isRelogin.show = false;
            Cookies.remove("Admin-Token");
            router.replace({ path: "/login" });
          })
          .catch(() => {
            isRelogin.show = false;
          });
      }
      return Promise.reject("");
    } else if (code === 500) {
      return Promise.reject(new Error(msg));
    }else if(code === 204){
      return res.status
    } 
    else {
      return res.data;
    }
  },
  (error) => {
    let { message } = error;
    if (message == "Network Error") {
      message = "";
    } else if (message.includes("timeout")) {
      message = "";
    } else if (message.includes("Request failed with status code")) {
      message = "" + message.substr(message.length - 3) + "";
    }
    console.log("error", error);
    return Promise.reject(error.response.data);
  }
);

export default service;
